body {
  margin: 0;
  padding: 0;
}
.partition {
  display: flex;
  flex-direction: row;
  flex: 1;
  width: 100%;
}

.editor {
  /* height: 100%; */
  width: 45vw;
  flex: 2;
}

.output {
  /* margin-left: 2px; */
  border-left: solid;
  border-width: 1px;
  height: 100vh;
  width: 100vw;
  flex: 2;
}

/* .editor {
  background-color: hsl(225, 6%, 25%);
} */

.editor_title {
  display: flex;
  justify-content: space-between;
  background-color: hsl(225, 6%, 13%);
  color: white;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  border-top-right-radius: 0.5rem;
  border-top-left-radius: 0.5rem; 
}

.CodeMirror {
  height: 100vh !important;
  font-size: 15px;
}

.code-mirror-wrapper {
  height: 100%;
  flex-grow: 1;
  border-bottom-right-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  overflow: hidden;
}
